import React, { useEffect, useState } from 'react';
import { useNavigate } from 'react-router-dom';

import Button from '../../components/Common/Button/Button';
import Input from '../../components/Common/Input/Input';
import TopNavigation from '../../components/Common/TopNavigation/TopNavigation';
import {
	validateFootSize,
	validateHeight,
	validateNickName,
	validateWeight,
	validateAllInput,
	validateDateOfBirth,
} from '../../utils/validate';
import nicknameImg from '../../assets/nickname.svg';
import { Radiobox } from '../../components/Common/CheckBox/Checkbox';

const JoinInfo = ({ info, onChangeHandler }) => {
	const [readyState, setReadyState] = useState(false);
	const [nickNameErrMsg, setNickNameErrMsg] = useState('');
	const navigate = useNavigate();

	useEffect(() => {
		const { state, type } = validateNickName(info.nickname);

		if (!state) {
			if (type === 'notLengthErr')
				setNickNameErrMsg(' , ,    .');
			else if (type === 'lengthErr') setNickNameErrMsg(' 2  8  .');
		}

		if (validateAllInput(info)) {
			setReadyState(true);
		} else {
			setReadyState(false);
		}
	}, [info]);

	return (
		<>
			<TopNavigation
				backClick
				onBackClick={() => {
					navigate(-1);
				}}
			/>
			<div
				className="wrapper"
				style={{
					padding: '30px',
					width: '100%',
					height: '100%',
					position: 'relative',
				}}
			>
				<h2 className="fs-24 fw-700" style={{ textAlign: 'center', paddingBottom: 30 }}>
					  
				</h2>
				<div
					style={{
						display: 'flex',
						'justify-content': 'center',
						width: '100%',
						'padding-bottom': '30px',
					}}
				>
					<Radiobox
						id="woman"
						type="radio"
						onClick={onChangeHandler}
						value=""
						name="gender"
						checked={info.gender === ''}
					/>
					
					<Radiobox
						id="man"
						type="radio"
						onClick={onChangeHandler}
						value=""
						name="gender"
						style={{ 'margin-left': '20px' }}
						checked={info.gender === ''}
					/>
					
				</div>
				<Input
					placeholder=""
					error={!validateDateOfBirth(info.dateOfBirth)}
					errMsg=" ."
					type="date"
					onChange={onChangeHandler}
					value={info.dateOfBirth}
					name="dateOfBirth"
					style={{
						padding: '20px',
						'font-size': '14px',
					}}
				/>
				<Input
					placeholder=""
					error={!(info.nickname.length === 0 || validateNickName(info.nickname).state)}
					errMsg={nickNameErrMsg}
					image={nicknameImg}
					type="text"
					onChange={onChangeHandler}
					value={info.nickname}
					name="nickname"
					style={{
						padding: '20px',
						'font-size': '14px',
					}}
				/>
				<Input
					placeholder=""
					error={!(info.height.length === 0 || validateHeight(info.height))}
					errMsg=" 90cm  250cm  ."
					unit="cm"
					type="number"
					name="height"
					onChange={onChangeHandler}
					value={info.height}
					style={{
						padding: '20px',
						'font-size': '14px',
					}}
				/>
				<Input
					placeholder=""
					error={!(info.weight.length === 0 || validateWeight(info.weight))}
					errMsg=" 30kg  200kg  ."
					unit="kg"
					type="number"
					name="weight"
					onChange={onChangeHandler}
					value={info.weight}
					style={{
						padding: '20px',
						'font-size': '14px',
					}}
				/>
				<Input
					placeholder=""
					error={!(info.footSize.length === 0 || validateFootSize(info.footSize))}
					errMsg=" 0mm  350mm  ."
					unit="mm"
					type="number"
					name="footSize"
					onChange={onChangeHandler}
					value={info.footSize}
					style={{
						padding: '20px',
						'font-size': '14px',
					}}
				/>
				<Button
					style={{
						position: 'absolute',
						bottom: 0,
						width: 'calc(100% - 60px)',
						marginBottom: 50,
					}}
					type={readyState ? 'active' : 'disabled'}
					label=" "
					onClick={() => navigate('/join/town')}
				/>
			</div>
		</>
	);
};

export default JoinInfo;
