import React, { useState, useEffect } from 'react';
/** @jsxImportSource @emotion/react */
import { css } from '@emotion/react';
import styled from 'styled-components';

import API from '../../api';
import TopNavigation from '../../components/Common/TopNavigation/TopNavigation';
import BottomNavigation from '../../components/Common/BottomNavigation/BottomNavigation';
import { useNavigate, useParams } from 'react-router-dom';
import null_profile from '../../assets/null_profile_img.png';
import gallery_img from '../../assets/gallery_img.png';
import ProfileTop from '../../components/Profile/ProfileTop';
import ProfileButton from '../../components/Profile/ProfileButton';
import { useUserDispatch, useUserState } from '../../context/User/UserContext';
import { getUserInfo } from '../../api/user';
import profile_menu from '../../assets/profile_menu.svg';
import Modal from '../../components/Common/Modal/Modal';
import colors from '../../constants/colors';
import { setLogout } from '../../context/User/UserTypes';

const Hr = styled.hr`
	border: none;
	border-bottom: 1px solid ${colors.grey50};
`;

const ProfileUser = ({ articleId }) => {
	const { userId } = useParams();
	const { loginedUserId } = useUserState();
	const navigate = useNavigate();
	const userDispatch = useUserDispatch();
	const [profileInfo, setProfileInfo] = useState(null);
	const [modalVisible, setModalVisible] = useState(false);

	const logoutClickHandler = () => {
		userDispatch(setLogout());
		API.defaults.headers.common['Authorization'] = '';
	};

	useEffect(() => {
		const fetch = async () => {
			const data = await getUserInfo(userId);

			if (data.result === 'fail') {
				navigate('/main');
			}
			setProfileInfo(data);
		};
		fetch();
	}, []);

	const openModal = () => {
		setModalVisible(true);
	};

	const closeModal = () => {
		setModalVisible(false);
	};

	return (
		<>
			{profileInfo && (
				<TopNavigation
					backClick
					onBackClick={() => navigate(-1)}
					leftContent={<div>{profileInfo.user.userId}</div>}
					rightMenu={
						loginedUserId === profileInfo.user.userId && (
							<img
								onClick={() => {
									openModal();
								}}
								src={profile_menu}
								alt=""
							/>
						)
					}
				/>
			)}

			<div
				className="wrapper"
				style={{
					width: '100%',
					height: '100%',
					overflowY: 'scroll',
				}}
			>
				{modalVisible && (
					<Modal visible={modalVisible} maskClosable onClose={closeModal}>
						<div
							css={css`
								text-align: center;
								font-family: 'Regular';
								font-size: 20px;
								line-height: 20px;
							`}
						>
							<div style={{ padding: '5px' }}>  </div>
							<Hr />
							<div style={{ padding: '5px' }}>  </div>
							<Hr />
							<div style={{ padding: '5px' }} onClick={() => logoutClickHandler}>
								
							</div>
							<Hr />
							<div style={{ padding: '5px' }}></div>
							<Hr />
						</div>
					</Modal>
				)}
				{/*   - ,  */}
				<div>
					{/*   */}
					{profileInfo && (
						<ProfileTop
							key={profileInfo.user.userId}
							imgSrc={null_profile}
							followerCount={profileInfo.userFollowerCount}
							followingCount={profileInfo.userFollowingCount}
							clothesCount={profileInfo.articleCount}
						/>
					)}
					{/*  */}
					{profileInfo && loginedUserId !== profileInfo.user.userId && (
						<ProfileButton isFollowed={profileInfo.isFollowed} setProfileInfo={setProfileInfo} />
					)}
				</div>
				{/*   */}
				<div
					css={css`
						display: flex;
						flex-direction: column;
					`}
				>
					{/*  */}
					<div
						css={css`
							display: grid;
							grid-template-columns: 1fr 1fr;
							grid-template-rows: auto;
							place-items: center;
							gap: 10px;
							padding: 10px;
						`}
					>
						<img
							css={css`
								display: flex;
								width: 100%;
								height: 100%;
							`}
							src={gallery_img}
							alt=""
							onClick={() => navigate(`/article/${articleId}`)}
						/>
					</div>
				</div>
			</div>
			<BottomNavigation />
		</>
	);
};

export default ProfileUser;
